
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import tabBar from "@/components/tabBar.vue";
import nullData from "@/components/nullData.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import { mapGetters, mapActions, mapMutations, mapState } from "vuex";
import { wxPay, share } from "@/utils/wx_sdk.js"
export default {
components: {
loading,
XHeader,
tabBar,
nullData
},
mounted(){
this.getstoreList()
},
data(){
return {
isLoading: true,
storeList: null,
sub_member_id: '',
orderList: null,
page: 1,
currentTab: 0
}
},
methods: {
checkTabbar(item){
let index = item._index;
if (this.currentTab == index) return;
var subid = item.sub_member_id;
this.currentTab = index;
this.orderList = null;
this.page = 1;
this.sub_member_id = subid;
this.getorderList();
},

async getstoreList(){
let data = {
plat: 3,
account: this.account,
token: this.token
}
const [err, res] = await api.sublist(data);
this.isLoading = false;
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
if(res.data.list.length > 0){
this.storeList = res.data.list;
this.sub_member_id = res.data.list[0].sub_member_id;
}else{
this.orderList = '';
}
this.getorderList();
}
},

async getorderList(){
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.page,
type: this.currentTab,
sub_member_id: this.sub_member_id
}
this.isLoading = true;
const [err, res] = await api.refitorderlist(data);
this.isLoading = false;
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
if(!this.orderList){
this.orderList = res.data.list;
}else{
if(res.data.list != ""){
this.orderList = this.orderList.concat(res.data.list);
}
}
this.page = this.page + 1;
}
},

confirm(index, orderid, type){
let data = {
plat: 3,
account: this.account,
token: this.token,
refit_id: orderid,
sub_member_id: this.sub_member_id
}
let that = this;
let text = '';
let fn = '';
switch (type){
case 'cancel':
text = '是否确认取消当前订单？';
fn = 'cancelDealReq';
break;
case 'sure':
text = '是否已收到货？';
fn = 'confirmReceipt';
break;
}
this.$vux.confirm.show({
content: text,
onConfirm () {
that[fn](data, index);
}
})
},

async cancelDealReq(data, index){
const [err, res] = await api.delrefit(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
this.orderList.splice(index, 1);
this.$vux.toast.text('取消成功');
}
},

async confirmReceipt(data, index){
const [err, res] = await api.confirmrefit(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
let newData = this.orderList[index];
newData.is_input = 2;
this.$set(this.orderList,index,newData);
this.$vux.toast.text('收货成功');
}
},

async pendingPayment(index, orderid){
this.$vux.loading.show({
text: '支付中...'
})
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
refit_id: orderid,
pay_code: 2,
sub_member_id: this.sub_member_id,
openid: this.user.openid
}
const [err, res] = await api.topay(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == '2000'){
var selfData = res;
let success = res =>{
that.orderInfo(selfData.data.order_id);
}
console.log(res)
wxPay(this,{...res.data.pay_param,success,debug: true})
}
console.log(this.user.openid)
},
orderInfo(orderid){
this.$router.push({
path: '/centerFull/myshop/replenishmentOrderinfo',
query: {
refit_id: orderid,
sub_member_id: this.sub_member_id
}
})
}
},
computed: {
...mapGetters(["user", "account", "token"])
},
}



